'use client';

import { Booking } from '@/src/lib/booking/BookingTypes';
import { Button } from '@/src/components/ui/button';
import { User, Phone, IdCard, Bike, Clock, Building2, CalendarDays } from 'lucide-react';
import { formatCurrency } from '@/src/utils/formatCurrency';
import { format } from 'date-fns';

interface Props {
  bookings: Booking[];
  stationNames: Record<string, string>;
  companyNames: Record<string, string>;
  packageNames: Record<string, string>;
  onEdit: (booking: Booking) => void;
  onDelete: (id: string) => void;
}

export default function BookingTable({
  bookings,
  stationNames,
  companyNames,
  packageNames,
  onEdit,
  onDelete,
}: Props) {
  if (!bookings.length) {
    return (
      <div className="text-center text-gray-500 p-8">
        No bookings found.
      </div>
    );
  }

  return (
    <div className="bg-white rounded-xl shadow p-4 overflow-x-auto mt-6">
      <table className="min-w-full text-sm border border-gray-200">
        <thead className="bg-gray-100 text-gray-700">
          <tr>
            <th className="px-3 py-2 border">Customer</th>
            <th className="px-3 py-2 border">Vehicle Info</th>
            <th className="px-3 py-2 border">Pricing</th>
            <th className="px-3 py-2 border">Rental Period</th>
            <th className="px-3 py-2 border">Station</th>
            <th className="px-3 py-2 border">Booked At</th>
            <th className="px-3 py-2 border">Actions</th>
          </tr>
        </thead>

        <tbody>
          {bookings.map((booking) => (
            <tr key={booking.id} className="hover:bg-gray-50">
              {/* Customer */}
              <td className="px-3 py-3 border align-top">
                <div className="flex flex-col gap-1">
                  <div className="flex items-center gap-2 font-semibold">
                    <User className="h-4 w-4 text-gray-500" />
                    {booking.fullName}
                  </div>
                  <div className="flex items-center gap-2 text-xs text-gray-600">
                    <Phone className="h-4 w-4" />
                    {booking.phone}
                  </div>
                  <div className="flex items-center gap-2 text-xs text-gray-600">
                    <IdCard className="h-4 w-4" />
                    {booking.idNumber}
                  </div>
                </div>
              </td>

              {/* Vehicle Info */}
              <td className="px-3 py-3 border align-top">
                <div className="flex flex-col gap-1">
                  <div className="flex items-center gap-2 font-medium">
                    <Bike className="h-4 w-4 text-green-600" />
                    {booking.vehicleModel}
                  </div>
                  <div className="text-xs text-gray-600 ml-6">
                    VIN: {booking.vin || 'N/A'}
                  </div>
                  <div className="text-xs text-gray-600 ml-6">
                    Plate: {booking.licensePlate || 'N/A'}
                  </div>
                </div>
              </td>

              {/* Pricing */}
              <td className="px-3 py-3 border align-top">
                <div className="text-green-600 font-bold text-lg">
                  {formatCurrency(booking.totalAmount)}
                </div>
                <div className="text-xs text-gray-600">
                  Package: {packageNames?.[booking.package ?? ''] || 'N/A'}
                </div>
                <div className="text-xs text-gray-600">
                  Deposit: {formatCurrency(booking.deposit)}
                </div>
                <div className="text-xs text-gray-600">
                  Remaining: {formatCurrency(booking.remainingBalance)}
                </div>
              </td>

              {/* Rental Period */}
              <td className="px-3 py-3 border align-top">
                {booking.rentalStartDate && booking.rentalEndDate ? (
                  <div className="flex flex-col gap-1">
                    <div className="flex items-center gap-2 text-gray-600">
                      <Clock className="h-4 w-4" />
                      {format(booking.rentalStartDate.toDate(), 'dd/MM/yyyy')}
                      {' ‚Üí '}
                      {format(booking.rentalEndDate.toDate(), 'dd/MM/yyyy')}
                    </div>
                    <div className="text-xs text-gray-500 ml-6">
                      ({booking.rentalDays} days)
                    </div>
                  </div>
                ) : (
                  <div className="flex items-center gap-2 text-gray-400 italic">
                    <Clock className="h-4 w-4" />
                    N/A
                  </div>
                )}
              </td>

              {/* Station */}
              <td className="px-3 py-3 border align-top">
                <div className="flex flex-col gap-1">
                  <div className="flex items-center gap-2 font-medium">
                    <Building2 className="h-4 w-4 text-blue-600" />
                    {stationNames?.[booking.stationId ?? ''] || 'N/A'}
                  </div>
                  <div className="text-xs text-gray-600 ml-6">
                    Company: {companyNames?.[booking.companyId ?? ''] || 'N/A'}
                  </div>
                </div>
              </td>

              {/* Booked At */}
              <td className="px-3 py-3 border align-top">
                <div className="flex items-center gap-2 text-gray-600">
                  <CalendarDays className="h-4 w-4" />
                  {booking.createdAt
                    ? format(booking.createdAt.toDate(), 'dd/MM/yyyy HH:mm')
                    : 'N/A'}
                </div>
              </td>

              {/* Actions */}
              <td className="px-3 py-3 border align-top">
                <div className="flex flex-col gap-2">
                  <Button
                    onClick={() => onEdit(booking)}
                    className="bg-green-500 hover:bg-green-600 text-white"
                  >
                    Edit
                  </Button>
                  <Button
                    variant="destructive"
                    onClick={() => onDelete(booking.id)}
                  >
                    Delete
                  </Button>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
